/** Simpler version `typographyTemplate` that does not set `color` and `margin` */
const typographyMixin = typography => {
  const {
    fontFamily,
    fontSize,
    fontWeight,
    lineHeight,
    letterSpacing,
    fontFeature,
    textDecoration,
    textTransform,
    fontStyle
  } = typography;
  return {
    fontFeatureSettings: fontFeature,
    fontFamily,
    fontSize,
    fontWeight,
    letterSpacing,
    lineHeight,
    textDecoration,
    textTransform: textTransform,
    fontStyle
  };
};

export { typographyMixin };
